<template>
    <div table-responsive>
        <table class="table table-sm table-hover">
            <thead class="thead-dark">
                <tr>
                    <th scope="col">성명</th>
                    <th scope="col">권한</th>
                    <th scope="col">삭제</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="user in this.userList" :key="user.uid">
                    <td scope="row">{{user.username}}</td>
                    <td scope="row"><input type="checkbox" v-model="user.isChecked">관리자</td>
                    <td scope="row"><input type="checkbox" v-model="user.isDeleted"></td>
                </tr>
            </tbody>
        </table>
    </div>
    <div class="footer">
        <div class="d-flex justify-content-center">
            <button class="btn btn-primary" @click="checkUserInfo" size="lg" >확 인</button>
        </div>
    </div>    
</template>

<script>
import {ref} from 'vue';
const in_users=ref([]);

export default{
    name: "UserInfo",
    data() {
        return{
            userList:in_users,
        }        
    },    
    mounted() {
        this.$store.dispatch("fetchAlluserList");
    },
    computed: {
        // getBackground() {
        // return this.user.isChecked ? "bg-success" : "bg-light";
        // },
        getAlluserList() {
            return this.$store.state.auth.userList;
        }
    },
    watch:{
        getAlluserList(val) {
            val.forEach( (user) => {
                let u = {
                    uid: user.uid,
                    email: user.email,
                    username: user.username,
                    admin: user.admin,
                    creat_at: user.create_at,
                    isChecked: user.admin==1? true: false,
                    isDeleted: false,
                }                
                this.userList.push(u);                
            });
        }
    },
    methods: {
        checkUserInfo() {
            this.userList.forEach( (user) => {
                console.log(user);
            });
        },
        // checkTodo() {
        // const todoData = {
        //     id: this.item.id,
        //     currentCheckState: this.item.isChecked,
        // };

        // this.$store.dispatch("checkTodo", todoData);
        // },

        // deleteTodo() {
        // this.$store.dispatch("deleteTodo", this.item.id);
        // },
    },
}



</script>